/* eslint-disable prefer-promise-reject-errors */
/* eslint-disable react/display-name */
/* eslint-disable react/destructuring-assignment */
import React, { useMemo, useState } from 'react';
import { Badge, Row } from 'reactstrap';

import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';

import ReactTable from '../../../../containers/application/ReactTable';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';

import AppError from '../../../../helpers/AppError';
import instance from '../../../../helpers/axiosInstance';

import useFetch from '../../../../hooks/use-fetch';

const Participants = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please Provide Hackathon ID');

  const { loading, data, error } = useFetch(`/getdetails/${id}`, 'getdetails');

  const cols = useMemo(
    () => [
      {
        Header: '#',
        accessor: 'id',
        cellClass: 'list-item-heading w-10',
        Cell: (props) => <>{props.value}</>,
      },
      {
        Header: 'Name',
        accessor: 'full_name',
        cellClass: 'w-30',
        Cell: (props) => <>{props.value}</>,
      },
      {
        Header: 'Submission',
        accessor: 'test',
        cellClass: 'w-20',
        Cell: (props) => <>{props.value}</>,
      },
      {
        Header: 'Status',
        accessor: 'status',
        cellClass: 'w-20',
        Cell: (props) => <StatusCell {...props} hackathon_id={id} />,
      },
    ],
    [id]
  );

  if (loading) return <div className="loading" />;

  if (error) throw new AppError(error);

  return (
    <>
      <Row className="">
        <Colxx xxs="12">
          <Breadcrumb heading="menu.participants" />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <ReactTable columns={cols} data={data.Participant} divided />
        </Colxx>
      </Row>
      <Row>
        {/* <Colxx><pre>{JSON.stringify(data, null, 2)}</pre></Colxx> */}
      </Row>
    </>
  );
};

const StatusCell = (props) => {
  const { hackathon_id, row } = props;

  const [status, setStatus] = useState(props.value);

  const handleUpdateStatus = (newStatus) => {
    return new Promise((resolve, reject) => {
      const updateStatus = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/changeuserstatus',
            data: {
              hackathon_id,
              user_id: row.original.user_id,
              status: newStatus,
            },
          });
          console.log(row.original.user_id);
          if (response.data.success) {
            resolve('Status Updated');
          } else {
            reject('Failed to Update Status');
          }
        } catch {
          reject('Failed to Update Status');
        }
      };

      updateStatus();
    });
  };
  return (
    <>
    {status !==0 ? (
      <>
      <StateButton
        id="hold-participant"
        size="xs"
        className="m-2"
        color="warning"
        onClick={() => handleUpdateStatus(0)}
        next={() => setStatus(0)}
      >
        On Hold
      </StateButton>
      </>
    ) : (<> </>)}
      {status === 0 ? (
        <>
        <StateButton
        id="accept-participant"
        size="xs"
        className="m-2"
        color="success"
        onClick={() => handleUpdateStatus(1)}
        next={() => setStatus(1)}
      >
        Approve
      </StateButton>
      <StateButton
        id="reject-participant"
        size="xs"
        className="m-2"
        color="danger"
        onClick={() => handleUpdateStatus(2)}
        next={() => setStatus(2)}
      >
        Reject
      </StateButton>
      <Badge
          pill
          size="xs"
          className="m-2"
          color= 'outline-warning'
          disabled
        >
          On Hold
        </Badge>
        </>
      ) : (
        <Badge
          pill
          size="xs"
          className="m-2"
          color={status === 1 ? 'outline-success' : 'outline-danger'}
          disabled
        >
          {status === 1 ? 'ACCEPTED' : 'REJECTED'}
        </Badge>
      )}

{status === 1 ? (
        <>
      <StateButton
        id="reject-participant"
        size="xs"
        className="m-2"
        color="danger"
        onClick={() => handleUpdateStatus(2)}
        next={() => setStatus(2)}
      >
        Reject
      </StateButton>
        </>
      ) : (
        <>
        </>
      )}
{status === 2 ? (
        <>
        <StateButton
        id="accept-participant"
        size="xs"
        className="m-2"
        color="success"
        onClick={() => handleUpdateStatus(1)}
        next={() => setStatus(1)}
      >
        Approve
      </StateButton>
        </>
      ) : (
        <></>
      )}
    </>
  );
};
export default Participants;
